#home{
  position: relative;
  /*transform: translate(0,0);*/
}
.home-src{
  /*position: fixed;*/
  /*top: 0;*/
  /*left: 0;*/
  width: 100%;
  /*background: #002981;*/
  height: 45px;
  z-index: 999;
  margin-top: .2rem;
}
.home-src-block{
  width: 90%;
  height: 30px;
  margin: 0 auto;
  position: relative;
}
.home-src-block input{
  width: 100%;
  height: 100%;
  border-radius: 20px;
  border: 1px solid #fff;
}
.home-src-block .iconfont{
  font-size: 20px;
  position: absolute;
  right:10px;
  top: 50%;
  margin-top: -10px;
}
.home-content{
  padding: 3%;
  background: -webkit-linear-gradient(90deg, #002981, #002981);
}
.home-menu{
  background: #fff;
  padding: 3%;
}
.home-menu-list{
}
.home-menu-item{
  width: 33.33%;
  text-align: center;
}
.home-menu-item i{
  font-size: 2rem;
  color: #2196f3;
}
.home-menu-item p{
  font-size: 12px;
  padding-top: .5rem;
  color: #2c3e50;
}
.home-news{
  margin-top: 5%;
}
.home-news-cap{
  width: 3.6rem;
  height: 3.6rem;
  border-radius: 5px;
  background: #005ea9;
  padding-top: .3rem;
  color: #fff;
  text-align: center;
}
.home-news-cap p{
  font-family: cursive;
  font-weight: bold;
  height: 1.5rem;
  line-height: 1.5rem;
}
.home-news-tag{
  width: 5rem;
  text-align: center;
}
.home-news-tag p{
  width: 3rem;
  text-align: center;
  color: #005ea9;
  font-size: 12px;
  border: 1px solid #005ea9;
  border-radius: 3px;
  padding: 1% 3%;
  margin: 7% auto 0;
}
.str_wrap {
  overflow:hidden;
//zoom:1;
  width:57%;
  font-size:12px;
  line-height:16px;
  position:relative;
  -moz-user-select: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background:#fff;
  white-space:nowrap;
}
.str_wrap.str_active {
  background:#fff;
}
.str_move {
  white-space:nowrap;
  position:absolute;
  top:22%;
  left:0;
  cursor:move;
}
.str_move_clone {
  display:inline-block;
//display:inline;
//zoom:1;
  vertical-align:top;
  position:absolute;
  left:100%;
  top:0;
}
.str_vertical .str_move_clone {
  left:0;
  top:100%;
}
.str_down .str_move_clone {
  left:0;
  bottom:100%;
}
.str_vertical .str_move,
.str_down .str_move {
  white-space:normal;
  width:100%;
}
.home-news-banner{
  padding: 1.8%;
}
.home-news-banner div{
}
.home-news-banner a{
  margin-right: 3rem;
}
.home-recommend{
  margin-top: 3%;
  background: #fff;
  padding: 3%;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.home-recommend-tit{
  font-size: .87rem;
}
.home-recommend-tit .iconfont{
  color: #999;
}
.home-recommend-list{
  margin-top:5%;
  width: 100%;
  white-space: nowrap;
  overflow-y: hidden;
}
.home-recommend-item{
  width: 23%;
  text-align: center;
  font-size: .75rem;
  display: inline-block;
}
.home-recommend-item-img{
  height: 4.8rem;
  overflow: hidden;
  margin-bottom: .3rem;
}
.home-recommend-item-img img{
  width: 95%;
  height: 100%;
}
.home-recommend-ly-list .home-recommend-item{
  width: 46%;
}
